import React, {useEffect, useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {PageHeaderAlt} from "components/layout-components/PageHeaderAlt";
import {Card, Typography, Form, Col, Switch, Row, Input, Button} from "antd";
import {CheckOutlined, CloseOutlined} from "@ant-design/icons";
import ReactQuill from 'react-quill';
import ColorPicker from "components/shared-components/ColorPicker";
import utils from "utils";
import CategoryTable from "components/shared-components/category-list/category-table";
import {_AddNewCategory} from "../../../../../redux/actions";

let {Item} = Form

const TravelCategoryAdd = (props) => {
    let { _AddNewCategory } = props
    const [form] = Form.useForm()
    const onChangeName = (e) => setSlug(e.target.value)
    const [slug, setSlug] = useState('')

    const [background,setBackground] = useState('#ffffff')
    const [quils] = useState({
        style: {
            height: '220px',
            margin: "0 0 40px 0"
        },
        modules: {
            toolbar: [
                [{header: [1, 2, 3, false]}],
                ['bold', 'italic', 'underline'],
                ['image', 'code-block']
            ]
        },
        theme: "snow",
        placeholder: "Compose your content..."

    })

    const onHeaderNavColorClick = (value) => {
        const {rgb} = value
        const rgba = `rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, ${rgb.a})`
        const hex = utils.rgbaToHex(rgba)
        setBackground(hex)
    }

    const onFinish = ()=> {
        form.validateFields()
            .then((values)=> {
                let formdata = new URLSearchParams()
                formdata.append('name',values?.name)
                formdata.append('about[title]',values?.about?.title)
                formdata.append('about[content]',values?.about?.content)
                formdata.append('slug',values?.slug)
                formdata.append('background',background)
                formdata.append('is_published',values?.is_published ? 1 : 0)
                formdata.append('is_verify',values?.is_verify ? 1 : 0)
                _AddNewCategory(formdata)

            })
    }

    return (
        <>
            <PageHeaderAlt background="/img/others/bg-doodle.jpg" cssClass="bg-primary" overlap>
                <div className="py-5 my-md-5 text-center">
                    <div>
                        <Typography.Title level={1} className={'text-white font-weight-bold'}>Add
                            Categories</Typography.Title>
                    </div>
                </div>
            </PageHeaderAlt>

            <div className="container my-4">
                <div style={{width: "100%", maxWidth: 1080, margin: "0 auto"}}>
                    <Card bordered={false} className={'mail-compose'}>
                        <Form

                            layout={'vertical'}
                            form={form}
                            onFinish={onFinish}
                            initialValues={{
                                name: "",
                                slug: "",
                                about: {
                                    title: "",
                                    content: ""
                                },
                                background: "",
                                is_published: true,
                                is_verify:true
                            }}
                        >
                            <Row gutter={24}>
                                <Col
                                    xs={{
                                        order: 1,
                                        span: 24,
                                    }}
                                    lg={{
                                        order: 1,
                                        span: 12
                                    }}
                                >
                                    <Item name={'name'} label={'Name'}
                                          rules={[{required: true, message: "Name is required!"}]}>
                                        <Input onChange={onChangeName}/>
                                    </Item>
                                </Col>
                                <Col
                                    xs={{
                                        order: 1,
                                        span: 24,
                                    }}
                                    lg={{
                                        order: 1,
                                        span: 12
                                    }}
                                >


                                    <Item label={'Slug'}>
                                        <Input disabled value={slug?.toString()?.toLowerCase()?.replace(/ /g, '-')}/>
                                    </Item>
                                </Col>

                                <Col
                                    xs={{
                                        order: 1,
                                        span: 24,
                                    }}
                                    lg={{
                                        order: 1,
                                        span: 24
                                    }}
                                >
                                    <Item label={'Title'} name={['about', 'title']}
                                          rules={[{required: true, message: "Title is required!"}]}>
                                        <Input/>
                                    </Item>
                                </Col>

                                <Col
                                    xs={{
                                        order: 1,
                                        span: 24,
                                    }}
                                    lg={{
                                        order: 1,
                                        span: 24
                                    }}
                                >
                                    <Item label={'Content'} name={['about', 'content']}>
                                        <ReactQuill {...quils}/>
                                    </Item>
                                </Col>
                                <Col
                                    xs={{
                                        order: 1,
                                        span: 8,
                                    }}
                                    lg={{
                                        order: 1,
                                        span: 6
                                    }}>

                                    <Item label={'Background'} name={'background'}>
                                        <ColorPicker color={'#fff'} colorChange={onHeaderNavColorClick}/>
                                    </Item>
                                </Col>
                                <Col
                                    xs={{
                                        order: 1,
                                        span: 8,
                                    }}
                                    lg={{
                                        order: 1,
                                        span: 6
                                    }}>

                                    <Item label={'Is Publish'} name={'is_published'}>
                                        <Switch
                                            checkedChildren={<CheckOutlined/>}
                                            unCheckedChildren={<CloseOutlined/>}
                                            defaultChecked
                                        />
                                    </Item>
                                </Col>
                                <Col
                                    xs={{
                                        order: 1,
                                        span: 8,
                                    }}
                                    lg={{
                                        order: 1,
                                        span: 6
                                    }}>

                                    <Item label={'Is Verify'} name={'is_verify'}>
                                        <Switch
                                            checkedChildren={<CheckOutlined/>}
                                            unCheckedChildren={<CloseOutlined/>}
                                            defaultChecked
                                        />
                                    </Item>
                                </Col>

                                <Col
                                    xs={{
                                        order: 1,
                                        span: 24,
                                    }}
                                    lg={{
                                        order: 1,
                                        span: 24
                                    }}>
                                    <Button htmlType={'submit'} type={'primary'}>Submit</Button>
                                </Col>
                            </Row>

                        </Form>
                    </Card>
                </div>
                <div style={{width: "100%", maxWidth: 1080, margin: "0 auto"}}>
                    <CategoryTable card={{title:"Category List",bordered:false}}/>
                </div>

            </div>
        </>
    )
}

TravelCategoryAdd.propTypes = {
    _AddNewCategory: PropTypes.func.isRequired
}
TravelCategoryAdd.defaultProps = {}

export default connect(() => {
    return {}
}, {_AddNewCategory})(TravelCategoryAdd)
